import * as React from "react";
import { Button, Toast } from 'antd-mobile';

import bg from '../../assets/img/logo.png'
import { getUserInfo } from '../../api/bind'
import './bind.less'
interface ISprops {
    [history: string]: any,
}
interface IState {
    [statesName: string]: any
}

export default class ChangePhone extends React.Component<ISprops, IState>{
    constructor(props: ISprops) {
        super(props);
        this.state = {
            phone: ''
        }
    }
    openid: string = ''
    componentDidMount() {
        document.title = '账号绑定';
        this.getUser()
    }
    getUser() {
        Toast.loading('')
        getUserInfo({}).then( (data) => {
            Toast.hide()
            if (!data.status) {
                this.setState({phone:data.data.cellphone})
            } else {
                Toast.fail(data["message"],2)
            }
        })
    }
    changePhone = () => {
        this.props.history.push('/bind/bindPhone?change=1')
    }
    render() {
        const { phone } = this.state;
        return (
            <div className="change-phone">
                <div className="logo">
                    <img src={bg} alt=""/>
                    <p>高顿会员服务中心</p>
                </div>

                <div className="show-phone">
                    绑定的手机号：{phone}
                </div>

                <Button type="primary" className="bind-btn" onClick={this.changePhone}>修改</Button>
                
                <div className="bind-tips">
                    *重要提示：请绑定您在报名时登记的手机号，若绑定号码与报名号码不一致，会导致您无法在微信中收取所报课程的相关信息。
                </div>
            </div>
        )
    }

}
